.cart {
  display: none;
}

.show-cart {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}
.cart-container {
  position: absolute;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #4d4d4d;
  padding: 0 0.125rem;
  transition: all 0.5s linear;
}

.cart-title {
  flex: 0 0 80px;
  padding: 0.125rem 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  margin-bottom: 0.125rem;
}
.cart-title h2 {
  color: yellow;
}

.cart-items-container::-webkit-scrollbar {
  width: 8px; /* 스크롤 너비 */
}

.cart-items-container::-webkit-scrollbar-track {
  background: #f1f1f1; /* 스크롤 트랙 배경 */
  border-radius: 10px;
}

.cart-items-container::-webkit-scrollbar-thumb {
  background: #888; /* 스크롤 thumb 색 */
  border-radius: 10px;
}

.cart-items-container::-webkit-scrollbar-thumb:hover {
  background: #555; /* 스크롤 thumb hover 시 색 */
}

.cart-items-container {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 120px;
  overflow: auto;
  background-color: #fafafa;
  padding-right: 0.125rem;
}

.cart-item {
  flex: 0 0 120px;
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  margin: 0.125rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  background-color: #e6e6e6;
}

.cart-item-header {
  flex: 0 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border: 1px solid red; */
}
.cart-item-header h3 {
  margin-left: 0.5rem;
  font-size: 1.1rem;
  /* border: 1px solid red; */
}
.cart-item-delete {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  width: 70px;
  height: 30px;
  margin-right: 0.5rem;
  cursor: pointer;
  font-weight: bold;
  color: #fff;
  background-color: #f44336;
  border-color: transparent;
  border-radius: 0.25rem;
  transition: all 0.3s linear;
}
.cart-selected-delete .icon {
  margin-right: 0.4rem;
  font-size: 0.8rem;
}
.cart-selected-delete span {
  font-size: 0.8rem;
  line-height: 1;
}
.cart-selected-delete:hover {
  background-color: #d32f2f;
  color: #ffeb3b;
}
.cart-item-details {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.25rem;
}
.cart-item-quantity {
  flex: 0 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* border: 1px solid red; */
}
.cart-item-quantity span {
  width: 30px;
  padding: 0.125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}

.cart-item-quantity .minus,
.cart-item-quantity .plus {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  padding: 0.125rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
}
.cart-item-quantity .minus:hover,
.cart-item-quantity .plus:hover {
  border: 1px solid black;
  border-radius: 0.25rem;
}

.cart-item-price {
  flex: 1;
  display: flex;
  justify-content: center;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
  font-size: 1rem;
}
.cart-item-total {
  flex: 0 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  background-color: #fafafa;
  border-top: 1px solid #888;
}
.cart-item-total-quantity,
.cart-item-total-price {
  font-size: 1.2rem;
  font-weight: bold;
  /* color: #333; */
  color: #000;
}
.cart-btns {
  flex: 0 0 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border-top: 1px solid rgba(255, 255, 255, 0.5); */
  /* padding: 0.125rem 0; */
  background-color: #000;
}
.cart-btns span {
  line-height: 1;
}
.cart-left,
.cart-right {
  height: 45px;
  /* height: 40px; */
  height: 100%;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  border-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
}
.cart-left {
  flex: 0 35%;
  background-color: #b0b0b0;
  color: #000;
}

.cart-right {
  flex: 1;
  background-color: #ffeb3b;
  color: #000;
}
.cart-left .icon,
.cart-right .icon {
  margin-right: 8px;
  font-size: 1.2rem;
  /* line-height: 1; */
  /* font-size: 1.4rem; */
  /* border: 1px solid red; */
}

.cart-left:hover {
  background-color: #757575;
  color: #fff;
}
.cart-right:hover {
  background-color: #fbc02d;
  color: #fff;
}
