* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  scrollbar-width: thin; /* Firefox 지원 */
  scrollbar-color: #888 #f1f1f1; /* Firefox 지원 */
}

/* Webkit 브라우저에 대한 스크롤 디자인 */
::-webkit-scrollbar {
  width: 8px; /* 스크롤 너비 */
}

::-webkit-scrollbar-track {
  background: #f1f1f1; /* 스크롤 트랙 배경 */
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #888; /* 스크롤 thumb 색 */
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555; /* 스크롤 thumb hover 시 색 */
}

main {
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  background-color: #000;
}
.main-container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  border: 1px solid #b0b0b0;
  border-radius: 0.25rem;
}

.main-center {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 80px 1fr 50px;
  position: relative;
}
nav {
  grid-column: 1 / 4;
}
aside {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
}
section {
  grid-column: 2 / 4;
  grid-row: 2 / 3;
}
footer {
  grid-column: 1 / 4;
  grid-row: 3 / 4;
}

@media screen and (max-width: 479px) {
  main {
    max-width: 100%;
    max-height: 100vh;
  }
  .main-center {
    grid-template-columns: 1fr;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  main {
    max-width: 480px;
    max-height: 700px;
  }

  .main-center {
    grid-template-columns: 1fr 2fr;
  }
  nav {
    grid-column: 1 / 3;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 3;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
    /* border: 1px solid red; */
    /* background-color: red; */
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
