    max-height: 800px;
  }

  .main-center {
    grid-template-columns: 1fr 3fr;
    /* border: 1px solid red; */
  }
  nav {
    grid-column: 1 / 4;
    grid-row: 1 / 2;
    /* border: 1px solid red; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 4;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 4;
    grid-row: 3 / 4;
    /* border: 1px solid red; */
  }
}

