    grid-column: 2 / 5;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 5;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  main {
    max-width: 1280px;
    max-height: 900px;
  }
  .main-center {
    grid-template-columns: 1fr 4fr;
  }
  nav {
    grid-column: 1 / 5;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 5;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 5;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}

@media screen and (min-width: 1440px) {
  .main-container {
    max-width: 1440px;
    max-height: 1000px;
