.order-confirmation {
  display: none;
}

.show-order-confirmation {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.order-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 80%;
  max-width: 800px;
  max-height: 600px;
  background-color: #4d4d4d;
  padding: 0.25rem;
  display: flex;
  flex-direction: column;
}
.order-title {
  flex: 0 0 60px;
  padding: 0.125rem 0.5rem;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  border: 1px solid red;
  background-color: #000;
}
.order-title h2 {
  font-size: 1.5rem;
  color: yellow;
}

.order-message {
  /* flex: 0 0 60px; */
  border: 1px solid red;
}

.order-items-list {
  flex: 1;
  border: 1px solid blue;
}
.order-total-quantity {
  flex: 0 0 50px;
  border: 1px solid red;
}
