.addItem-container {
  height: 100%;
  display: flex;
  align-items: center;
  /* justify-content: flex-end; */
  justify-content: space-between;
  padding-right: 1rem;
  padding-left: 1rem;
  /* border-top: 1px solid #b0b0b0; */
  /* border-top: 1px solid red; */
}

.addItem-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.call-btn,
.left-btn,
.right-btn {
  margin: 0 0.5rem;
  width: 120px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-size: 0.9rem; */
  font-size: 1rem;
  font-weight: bold;
  background-color: #fff;
  color: #000;
  border-radius: 0.25rem;
  cursor: pointer;
}
.call-btn .icon,
.left-btn .icon,
.right-btn .icon {
  font-size: 1.4rem;
  font-size: 1.2rem;
  margin-right: 8px;
  /* line-height: 1; */
  /* margin-right: 12px; */
  /* border: 1px solid red; */
}
.addItem-container span {
  line-height: 1;
}
.cart-order {
  position: relative;
}
.cart-amount {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #f44336;
  color: #fff;
  font-size: 0.8rem;
  font-weight: bold;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.shake {
  background-color: #f44336;
  color: #fff;
  animation: shake 0.5s linear;
  animation-iteration-count: 4;
}
@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  20%,
  60% {
    transform: translateX(-2px);
  }
  40%,
  80% {
    transform: translateX(2px);
  }
}
